<template>
  <v-card flat>
    <v-form @submit.prevent="savep">
      <v-container>
        <!-- alert -->
        <!-- avatar -->
        <!-- name -->
        <v-divider class="mx-0"></v-divider>
        <v-row wrap style="margin-top: 25px">
          <v-col cols="12" sm="12" class="mb-0 py-0">
            <v-text-field
              v-model="form.code_user"
              color="#007bff"
              :label="$vuetify.lang.t('$vuetify.users.cols.code_user.title')"
              outlined
              prepend-inner-icon="mdi-text-short"
              dense
              :value="form.code_user"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" sm="12" class="mb-0 py-0">
            <v-text-field
              v-model="form.position_held"
              color="#007bff"
              :label="
                $vuetify.lang.t('$vuetify.users.cols.position_held.title')
              "
              outlined
              dense
              prepend-inner-icon="mdi-text-short"
              :value="form.position_held"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-divider class="mx-0 mt-3"></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#007bff" :loading="loading" dark type="submit">
            {{ $vuetify.lang.t("$vuetify.btn.save") }}
          </v-btn>
        </v-card-actions>
      </v-container>
    </v-form>
  </v-card>
</template>

<script>
// import {
//   required,
//   requiredIf,
//   sameAs
// } from "vuelidate/lib/validators";
// // import { phoneR } from "../../helpers/Validator";
import FormMixin from "./../../mixins/Common/Form.vue";

export default {
  name: "UpdateProfile",
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          name: null,
          dateNaiss: null,
          dateService: null,
          intervention_days: null,
          position: null,
          address: null,
          speciality: null,
          phone: null,
          email: null,
          username: null
        };
      }
    }
  },
  data: () => ({
    form: {
      name: null,
      dateNaiss: null,
      dateService: null,
      intervention_days: null,
      position: null,
      address: null,
      speciality: null,
      phone: null,
      email: null,
      username: null
    },
    urlDepartments: "departments",
    isLoading: false,
    loading: false,
    search: null,
    itemsDepartments: [],
    menu2: false,
    menu: false,

    optionDays: {
      type_Days: []
    },
    optionPos: {
      type_Pos: []
    },
    urlItems: "/profile",
    types: {
      role: []
    },
    itemsRoles: []
  }),
  watch: {
    search(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlDepartments,
          params: params
        })
        .then(response => {
          //console.log("respp", response.data.content);
          self.itemsDepartments = response.data.content;
        })
        .finally(() => (this.isLoading = false));
    }
  },
  created() {
    this.form.name = this.$auth.user().profile.name;
    this.form.email = this.$auth.user().profile.email;
    this.form.phone = this.$auth.user().profile.phone;
    this.form.position = this.$auth.user().profile.position;
    this.form.dateNaiss = this.$auth.user().profile.dateNaiss;
    this.form.dateService = this.$auth.user().profile.dateService;
    this.form.intervention_days = this.$auth.user().profile.intervention_days;
    this.form.address = this.$auth.user().profile.address;
    this.form.speciality = this.$auth.user().profile.speciality;
    this.form.departments = this.$auth.user().profile.departments;
    this.form.username = this.$auth.user().username;
    this.getOptionsJour();
    this.getOptionsPosition();
    //this.setItemRoles();
  },
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    getOptionsJour() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.days, type_Days => {
        self.optionDays.type_Days.push({
          text: self.$vuetify.lang.t(type_Days.text),
          value: type_Days.value
        });
      });
    },
    getOptionsPosition() {
      // On pouvait aussi utliser le forEach au lieu de   _.eaach
      let self = this;
      this._.each(this.$store.getters.position, type_Pos => {
        self.optionPos.type_Pos.push({
          text: self.$vuetify.lang.t(type_Pos.text),
          value: type_Pos.value
        });
      });
    },
    reloadPage() {
      window.location.reload();
    },

    // Fonction pour récuperer le contenu des champs du formulaire
    getDataFormP() {
      let data = {
        intervention_days: this.form.intervention_days,
        position: this.form.position,
        dateNaiss: this.form.dateNaiss,
        dateService: this.form.dateService,
        address: this.form.address,
        speciality: this.form.speciality,
        name: this.form.name,
        phone: this.form.phone,
        email: this.form.email,
        departments: this.form.departments,
        username: this.form.username
      };
      return data;
    },
    setForm() {
      // Données envoyées lors de la modification
      this.form.id = this.$auth.user().id;
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
    usernameErrors() {
      let errors = [];
      if (!this.$v.form.username.$dirty) return errors;

      !this.$v.form.username.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.users.cols.username.required")
        );

      !this.$v.form.username.minLength &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.users.cols.username.minLength",
            this.$v.form.username.$params.minLength.min
          )
        );

      !this.$v.form.username.maxLength &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.users.cols.username.maxLength",
            this.$v.form.username.$params.maxLength.max
          )
        );
      !this.$v.form.username.unique &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.users.cols.username.unique")
        );
      return errors;
    },
    lastnameErrors() {
      let errors = [];
      if (!this.$v.form.lastName.$dirty) return errors;
      !this.$v.form.lastName.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.users.cols.last_name.required")
        );

      !this.$v.form.lastName.maxLength &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.users.cols.last_name.maxLength",
            this.$v.form.lastName.$params.maxLength.max
          )
        );
      return errors;
    }
  }
};
</script>
